<template>
  <section class="content">
    <div class="container-fluid">
        <div class="row">

          <div class="col-12">
        
            <div class="card" v-if="$gate.isAdmin()">
              <div class="card-header">
                <h3 class="card-title">model List</h3>

                <div class="card-tools">
                  
                  <button type="button" class="btn btn-sm btn-primary" @click="newModal">
                      <i class="fa fa-plus-square"></i>
                      Add New
                  </button>
                </div>
              </div>
              <!-- /.card-header -->
              <div class="card-body table-responsive p-0">
                <table class="table table-hover">
                  <thead>
                    <tr>
                      <th>Sl. No</th>
                      <th>Model Name</th> 
                      <th>Make</th>
                      <th>Created By</th>
                      <th>Created On</th>
                      <!-- <th>Action</th> -->
                    </tr>
                  </thead>
                  <tbody>
                     <tr v-for="(model,x ) in Models.data" :key="model.id">

                      <td>{{Models.from + x}}</td>
                      <td class="text-capitalize">{{model.name}}</td> 
                      <td>{{model.company?model.company.name:''}}</td>
                      <td>{{model.created_by_name  }}</td>
                      <td>{{model.created_at|myDate}}</td>
                      <!-- <td> 
                        <a href="javascript:void(0);" @click="editModal(model)" class="btn btn-success btn-sm">
                           Edit
                        </a>
                      </td> -->
                    </tr>
                  </tbody>
                </table>
              </div>
              <!-- /.card-body -->
              <div class="card-footer">
                  <pagination :data="Models" @pagination-change-page="getResults"></pagination>
              </div>
            </div>
            <!-- /.card -->
          </div>
        </div>


        <div v-if="!$gate.isAdmin()">
            <not-found></not-found>
        </div>

        <!-- Modal -->
        <div class="modal fade" id="addNewmodel" tabindex="-1" role="dialog" aria-labelledby="addNewmodel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" v-show="!editmode">Create New model</h5>
                    <h5 class="modal-title" v-show="editmode">Update model</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>

                <!-- <form @submit.prevent="createUser"> -->

                <form @submit.prevent="editmode ? updatemodel() : createmodel()">
                    <div class="modal-body">
                        <div class="form-group">
                            <label>Name</label>
                            <input v-model="form.name" type="text" name="name"
                                class="form-control" :class="{ 'is-invalid': form.errors.has('name') }" ref="name">
                            <has-error :form="form" field="name"></has-error>
                        </div> 
                        <div class="form-group">
                            <label>Machine Company</label>
                            <v-select label="name" :reduce="(option) => option.id" :options="companies" placeholder="Choose Company ..." v-model="form.company_id" :class="{ 'is-invalid': form.errors.has('company_id') }" >
                            </v-select>
                            <has-error :form="form" field="company_id"></has-error>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        <button v-show="editmode" type="submit" class="btn btn-success">Update</button>
                        <button v-show="!editmode" type="submit" class="btn btn-primary">Create</button>
                    </div>
                  </form>
                </div>
            </div>
        </div>
    </div>
  </section>
</template>

<script>
    export default {
        data () {
            return {
                editmode: false,
                Models : {}, 
                companies:[],
                form: new Form({
                    id : '',
                    name: '',
                    company_id: '',
                })
            }
        },
        methods: {

            getResults(page = 1) {

                  this.$Progress.start();
                  
                  axios.get('/api/model?page=' + page).then(({ data }) => (this.Models = data.data));

                  this.$Progress.finish();
            },
            updatemodel(){
                this.$Progress.start();
                this.form.put('/api/model/'+this.form.id)
                .then((response) => {
                    // success
                    $('#addNewmodel').modal('hide');
                    Toast.fire({
                      icon: 'success',
                      title: response.data.message
                    });
                    this.$Progress.finish();
                        //  Fire.$emit('AfterCreate');

                    this.loadModels();
                })
                .catch(() => {
                    this.$Progress.fail();
                });

            },
            editModal(model){
                this.editmode = true;
                this.form.reset();
                $('#addNewmodel').modal('show');
                 this.$refs.name.focus();
                this.form.fill(model);
            },
            newModal(){
                this.editmode = false;
                this.form.reset();
                $('#addNewmodel').modal('show');
                 this.$refs.name.focus();
            },

            loadModels(){
                if(this.$gate.isAdmin()){
                    axios.get("/api/model").then(({ data }) => (this.Models = data.data)); 
                }
            },
            
            createmodel(){

                this.form.post('/api/model')
                .then((response)=>{
                    $('#addNewmodel').modal('hide');

                    Toast.fire({
                            icon: 'success',
                            title: response.data.message
                    });

                    this.$Progress.finish();
                    this.loadModels();
                })
                .catch(()=>{
                    Toast.fire({
                        icon: 'error',
                        title: 'Some error occured! Please try again'
                    });
                })
            },
             

        },
        mounted() {
           
        },
            beforeCreate() {
        axios.get("api/company/list")
            .then((res) => {
                this.companies = res.data.data;
            })
    },
        created() {

            this.$Progress.start();
            this.loadModels();
            this.$Progress.finish();
        }
    }
</script>
